.articles {
    position: relative;
    z-index: 2;
    margin-bottom: -171px;

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 45px;

        @extend %dms-bold;
    }

    &__title {
        margin-bottom: 0;
        text-align: left;
    }

    &__next {
        position: relative;
        padding-right: 25px;
        font-size: 16px;
        color: $grey-500;

        svg {
            position: absolute;
            right: 0;
            top: 3px;
            width: 7px;
            height: 12px;
            fill: $grey-500;
        }

        @extend %hover-link;
    }

    &__items {
        display: flex;
    }
}

.article-card {
    display: flex;
    flex-direction: column;
    width: 100%;
    border-radius: 5px;
    box-shadow: 0 6px 47px 3px rgba(37, 37, 37, 0.05);

    &__element {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    &:not(:last-child) {
        margin-right: 30px;
    }

    &__img-wrap {
        position: relative;
    }

    &__img {
        width: 100%;
        height: 250px;
        border-radius: 5px 5px 0 0;
        object-fit: cover;
    }

    &__subtitle {
        position: absolute;
        left: 28px;
        bottom: -15px;
        padding: 8px 31px;
        border: 1px solid transparent;
        border-radius: 15px;
        font-size: 14px;
        text-transform: uppercase;
        color: $white;
        background-color: $main;
        transition: color 0.5s, background-color 0.5s, border-color 0.5s;

        &:hover {
            border-color: $main;
            color: $main;
            background-color: $white;
        }

        @extend %poppins-bold;
    }

    &__info {
        display: flex;
        flex-direction: column;
        padding: 35px 30px 25px;
        height: 100%;
        background-color: $white;
    }

    &__title {
        @include line-clamp(3);

        font-size: 18px;
        color: $grey-500;

        @extend %hover-link;
        @extend %dms-medium;
    }

    &__link {
        margin-bottom: 15px;
    }

    &__bottom {
        display: flex;
        margin-top: auto;
        padding-top: 17px;
        border-top: 1px solid $grey-75;
        font-size: 12px;
        text-transform: uppercase;
        color: #222933;
    }

    &__descr--date::before {
        background-image: url("#{$img}articles/1.svg");
    }

    &__descr--author::before {
        background-image: url("#{$img}articles/2.svg");
    }

    &__user {
        @extend %hover-link;
    }

    &__descr {
        @include line-clamp(1);

        position: relative;
        padding-left: 18px;

        &--date {
            padding-right: 30px;
        }
    }

    &__descr::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 14px;
        height: 17px;
        background-size: contain;
        background-repeat: no-repeat;
    }
}