.subscribe-form {
    display: flex;

    &__input {
        padding: 12px 15px;
        width: 100%;
        max-width: 170px;
        border: none;
        background-color: rgba($bg, 0.2);

        &::placeholder {
            font-size: 12px;
            color: $blue-500;

            @extend %dms-regular;
        }
    }

    &__btn {
        padding: 12px 16px;
        border: none;
        color: $white;
        background-color: $main;

        @extend %dms-regular;
        @extend %hover-nav-header;
    }
}